@base-color: #000000;

.home-page {
  background: url(/static/media/florida-crossover.bdd56c58.jpg) no-repeat center center fixed;
  background-size: cover;
  height: 100vh;
}

.info-page {
  background: url(/static/media/villa-parker.1af77b03.jpeg) no-repeat center center;
  /* height: 70vh; */
  background-size: cover;
}

